export default {
  itemsComputed () {
    let items = this.items

    if (typeof this.items === 'string') {
      items = JSON.parse(this.items)
    }

    return this.filteredBySearchItems(items)
  },
  itemsComputedWithScrollLimit () {
    return this.itemsComputed.slice(0, this.scrollItemsLimitCurrent)
  },
  inputValue () {
    // ÐµÑÐ»Ð¸ ÑƒÐºÐ°Ð·Ð°Ð½ ÑÐ»Ð¾Ñ‚ selection, Ñ‚Ð¾ Ð½Ðµ Ð½Ð°Ð´Ð¾ Ð¾Ñ‚Ð¾Ð±Ñ€Ð°Ð¶Ð°Ñ‚ÑŒ Ñ‚ÐµÐºÑÑ‚ Ð² Ð¸Ð½Ð¿ÑƒÑ‚Ðµ, Ð¾Ð½ Ñ‚Ð¾Ð»ÑŒÐºÐ¾ Ð¼ÐµÑˆÐ°ÐµÑ‚
    if (this.$scopedSlots.selection && this.getSearchData() === '') return ''
    // ÐµÑÐ»Ð¸ ÐµÑÑ‚ÑŒ ÑÑ‚Ñ€Ð¾ÐºÐ° Ð¿Ð¾Ð¸ÑÐºÐ°, Ñ‚Ð¾ Ð¿ÑƒÑÑ‚ÑŒ Ð¾Ð½Ð° Ñ‚Ð°Ð¼ Ð±ÑƒÐ´ÐµÑ‚
    if (this.getSearchData() !== '') return this.getSearchData()

    // Ð¸Ð½Ð°Ñ‡Ðµ Ð¿ÑƒÑÑ‚ÑŒ Ð±ÑƒÐ´ÐµÑ‚ Ñ‚ÐµÐºÑÑ‚ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð° Ð¸Ð»Ð¸ ÐµÐ³Ð¾ Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ðµ
    return this.getItemText(this.selectedItem) || this.currentItemValue
  },
  currentItemValue () {
    return this.getItemValue(this.selectedItem)
  },
  showSelectionSlot () {
    return this.$scopedSlots.selection && this.selectedItem && !this.getSearchData()
  },
  inputForTextStyles () {
    if (this.loading) {
      return {
        'background-image': `url(${this.loadingIndicator})`
      }
    }

    return {}
  },
  hasMenu () {
    return this.wishShowMenu && !this.loading // this.focused && !this.loading
  },
  hasError () {
    return !!this.errorMessage
  },
  isMobile () {
    // return window.innerWidth + window.innerHeight <= 1800
    return window.innerWidth <= 900 && window.innerHeight <= 900
  },
  menuDynamicStyles () {
    let obj = {
      // ÑˆÐ¸Ñ€Ð¸Ð½Ð° Ñ‚Ð°ÐºÐ°Ñ Ð¶Ðµ ÐºÐ°Ðº Ð¸ Ñƒ Ð¿Ð¾Ð»Ñ Ð²Ð²Ð¾Ð´Ð°
      'width': this.$refs['IZ-select__input'].offsetWidth + 'px',
      'pointer-events': this.hasMenu ? 'auto' : 'none'
    }

    if (this.disableSearch) {
      obj.top = this.$refs['IZ-select__input'].offsetTop + 'px'
    }

    return obj
  },
  // get item index from arr
  selectedItemIndex () {
    for (let itemKey in this.itemsComputed) {
      if (this.selectedItem === this.itemsComputed[itemKey] && this.itemsComputed.hasOwnProperty(itemKey)) {
        return itemKey
      }
    }

    return null
  }
}
